import { Link } from 'react-router-dom';
import lugLinkStyles from './lugLink.module.css';

export default function LugLink({ link, children }) {
    return (
        <Link
            className={lugLinkStyles.index}
            to={link}
            target='_blank'
            rel='noopener noreferrer'
        >
            {children}
        </Link>
    );
}

